.login {
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: #000;
}

.login > img {
    width: 100%;
}

.login > a {
    padding: 20px;
    border-radius: 99px;
    background-color: #1db954;
    color: #fff;
    font-weight: 800;
    text-decoration: none;
    cursor: pointer;
}