@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.login {
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: #000;
}

.login > img {
    width: 100%;
}

.login > a {
    padding: 20px;
    border-radius: 99px;
    background-color: #1db954;
    color: #fff;
    font-weight: 800;
    text-decoration: none;
    cursor: pointer;
}

.body {
    height: 100vh;
    -webkit-flex: 0.8 1;
            flex: 0.8 1;
    /* background-color: rgb(91, 87, 115); */
    background: linear-gradient(rgb(91, 87, 115), rgba(0, 0, 0, 1));
    color: white;
    padding: 30px;
    overflow-y: overlay;
}

.body::-webkit-scrollbar {
    display: none;
}

.body__info {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    padding: 10px;
}

.body__infoText {
    -webkit-flex: 1 1;
            flex: 1 1;
}

.body__infoText > h2 {
    font-size: 48px;
    margin-bottom: 10px;
}

.body__infoText > p{
    font-size: 14px;
}

.body__info > img {
    height: 20vw;
    margin: 0 20px;
    box-shadow: 0 4px 60px rgba(0, 0, 0, 0.5);
}

.body__icons {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.body__icons > .MuiSvgIcon-root {
    margin-right: 20px;
}

.body__shuffle {
    font-size: 80px !important;
    margin-left: 50px;
    margin-top: 20px;
    margin-bottom: 20px;
}

.body__shuffle:hover {
    transition: 100ms -webkit-transform ease-in;
    transition: 100ms transform ease-in;
    transition: 100ms transform ease-in, 100ms -webkit-transform ease-in;
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
}

.body__songs {
    margin: 20px -30px;
}
.header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-bottom: 30px;
}

.header__left {
    background-color: #fff;
    color: gray;
    border-radius: 30px;
    display: -webkit-flex;
    display: flex;
    padding: 10px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex: 0.6 1;
            flex: 0.6 1;
    min-width: 70px;
}

.header__left > input {
    border: none;
    width: 100%;
    outline: none;
}

.header__right {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.header__right > h4 {
    margin-left: 10px;
}
.songRow {
    margin-left: 20px;
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: #fff;
}

.songRow:hover {
    cursor: pointer;
    background-color: black;
    opacity: 0.8;
}

.songRow__info {
    margin-left: 20px;
}

.songRow__info > h1 {
    font-size: 16px;
}

.songRow__info > p {
    font-size: 14px;
    margin-top: 3px;
    color: grey;
}

.songRow__album {
    height: 40px;
    width: 40px;
}
.footer {
    position: fixed;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    display: -webkit-flex;
    display: flex;
    bottom: 0;
    height: 65px;
    width: 97%;
    background-color: #282828;
    padding: 20px;
}

.footer__left {
    -webkit-flex: 0.3 1;
            flex: 0.3 1;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: white;
    max-width: 300px;
}

.footer__center {
    -webkit-flex: 0.4 1;
            flex: 0.4 1;
    padding: 0 100px;
    color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    max-width: 300px;
}

.footer__right {
    -webkit-flex: 0.3 1;
            flex: 0.3 1;
    color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.footer__albumLogo {
    height: 60px;
    width: 60px;
    object-fit: contain;
    margin-right: 20px;
}

.footer__songInfo > h4 {
    margin-bottom: 5px;
}

.footer__songInfo > p {
    font-size: 12px;
}

.footer__green { 
    color: #1ed15e;
}

.footer__icon:hover,
.footer__green:hover {
    transition: -webkit-transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
    -webkit-transform: scale(1.2) !important;
            transform: scale(1.2) !important;
}

.footer__right * .MuiSlider-root {
    color: green;
}
.player__body {
    display: -webkit-flex;
    display: flex;
}
.sidebar {
    height: 100vh;
    -webkit-flex: 0.2 1;
            flex: 0.2 1;
    background-color: #040404;
    color: white;
    min-width: 230px;
    padding-left: 10px;
    padding-right: 10px;
}

.sidebar > hr {
    border: 1px solid #282828;
    width: 90%;
    margin: 10px auto;
}

.sidebar > img {
    margin-right: auto;
    object-fit: contain;
    height: 70px;
    padding: 10px;
}

.sidebar__title {
    margin-left: 10px;
    padding: 5px;
    font-size: 12px;
}


.sidebarOption {
    display: -webkit-flex;
    display: flex;
    color: grey;
    height: 40px;
    cursor: pointer;
    transition: 200ms color ease-in;
    -webkit-align-items: center;
            align-items: center;
}

.sidebarOption:hover {
    color: white;
}

.sidebarOption__icon {
    padding-left: 10px;
    padding-right: 10px;
}

.sidebarOption > p {
    margin-top: 10px;
    margin-left: 20px;
    font-size: 14px;
}
body::-webkit-scrollbar {
    display: none;
}

* {
    margin: 0;
    font-family: 'Roboto', sans-serif;
}
